import { supabase } from '../lib/supabaseClient';

export async function fetchReflection(answer: string): Promise<string> {
  const prompt = `Stell dir vor, du bist die beste Freundin  warmherzig, direkt, ehrlich, liebevoll und einfhlsam. Du antwortest locker, wie im echten Chat, niemals wie ein professioneller Coach. Analysiere die folgende Wochenreflexion und gib eine Rckmeldung in 2-3 Stzen, die sich anfhlt wie von einer besten Freundin: ehrlich, bestrkend, manchmal frech, aber immer herzlich. Keine Floskeln, keine Distanz, sondern echtes Mitgefhl und Nhe. Sprich die Person direkt an, aber verzichte auf knstliche Anreden wie 'Liebe/r [Name]'. Beende deine Rckmeldung immer mit einer wertenden, freundschaftlichen Aussage  stelle keine Fragen und gib keine Aufforderungen. Hier ist die Reflexion: "${answer}"`;

  // Supabase-Session holen
  const { data: { session } } = await supabase.auth.getSession();
  if (!session || !session.access_token) throw new Error('Nicht eingeloggt');

  const response = await fetch("https://vifbqtzkoytsgowctpjo.functions.supabase.co/openai-proxy", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      "Authorization": `Bearer ${session.access_token}`,
    },
    body: JSON.stringify({
      model: "gpt-3.5-turbo",
      messages: [{ role: "user", content: prompt }],
      max_tokens: 500,
    }),
  });

  if (!response.ok) {
    throw new Error("OpenAI API Fehler");
  }

  const data = await response.json();
  return data.choices[0].message.content;
} 